import { FC, useContext, useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { AppContext } from "../../App";
import { getChats } from "../../helpers/api";
import { getPhotoURL } from "../../helpers/getPhotoURL";
import { preloadImagesOfDifferentUsers } from "../../helpers/preloadImages";
import { IChatPreview } from "../../interfaces/IChatPreview";
import "./Chats.scss";

const Chats: FC = () => {
  const { isLoading, setIsLoading } = useContext(AppContext);

  const [chats, setChats] = useState<IChatPreview[]>([]);

  useEffect(() => {
    const effect = async () => {
      const chats = await getChats();
      setChats(chats);

      const imagesToPreload = chats.map((chat) => ({
        userId: chat.collaborator.userId,
        photoId: chat.collaborator.photoId,
      }));
      await preloadImagesOfDifferentUsers(imagesToPreload);

      setIsLoading(false);
    };

    effect();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  if (isLoading) return null;

  return (
    <div className="chats">
      {!chats.length && (
        <div className="no-chats">
          <p>Тут будуть твої чати</p>
          <span className="material-symbols-rounded">sentiment_satisfied</span>
        </div>
      )}

      {chats.map((chat, index) => (
        <Link
          to={`/chats/${chat.chatId}/${chat.collaborator.photoId}/${chat.collaborator.userId}/${chat.collaborator.name}`}
          className={`chat-preview ${!chat.isRead ? "not-read" : ""}`}
          onClick={() => {
            setIsLoading(true);
          }}
          key={`chat-${index}`}
        >
          <div
            className="avatar"
            style={{
              backgroundImage: `url(${getPhotoURL(
                chat.collaborator.userId,
                chat.collaborator.photoId
              )})`,
            }}
          />
          <div className="info">
            <div className="name">{chat.collaborator.name}</div>
            <div className={`preview-text ${!chat.isRead ? "not-read" : ""}`}>
              {chat.isMatch && "Є взаємна симпатія ❤️"}
              {chat.isByMe && "Я: "}
              {chat.previewMessage}
            </div>
          </div>
        </Link>
      ))}
    </div>
  );
};

export default Chats;
