import { FC, useContext, useEffect, useRef, useState } from "react";
import { Link, useParams } from "react-router-dom";
import { AppContext } from "../../App";
import { getMessages, sendMessage } from "../../helpers/api";
import { getPhotoURL } from "../../helpers/getPhotoURL";
import { IMessage } from "../../interfaces/IMessage";
import "./Chat.scss";

const Chat: FC = () => {
  const { chatId, userId, photoId, name } = useParams();

  const { isLoading, setIsLoading } = useContext(AppContext);

  const [messages, setMessages] = useState<IMessage[]>([]);
  const [oldMessagesLenght, setOldMessagesLenght] = useState<number>(0);
  const [textareaValue, setTextareaValue] = useState("");
  const [isFirstLoading, setIsFirstLoading] = useState<boolean>(true);
  const [isScrolledToBottom, setIsScrolledToBottom] = useState<boolean>(true);

  const chatRef = useRef<HTMLDivElement>(null);
  const canMessagesBeUpdatedRef = useRef<boolean>(true);

  useEffect(() => {
    setIsLoading(true);

    window.onscroll = () => {
      setIsScrolledToBottom(
        // 70 - top, 71 - send-container, 40 - overlay
        window.innerHeight - 70 - 71 + 40 + window.pageYOffset >=
          document.body.offsetHeight
      );
    };

    updateMessages();
    const interval = setInterval(updateMessages, 3000);

    return () => {
      clearInterval(interval);
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    if (isFirstLoading && messages.length) {
      scrollMessages();
      setIsFirstLoading(false);
    } else if (
      // if there is new message from a person and messages is scrolled to bottom - scroll to down
      messages.length > oldMessagesLenght &&
      !messages.at(-1)?.isByMe &&
      isScrolledToBottom
    ) {
      scrollMessages();
    }

    setOldMessagesLenght(messages.length);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [messages]);

  const onSend = async () => {
    if (!chatId || textareaValue.trim() === "") return;

    setMessages((prev) => [...prev, { isByMe: true, payload: textareaValue }]);

    setTimeout(() => {
      setTextareaValue("");
      scrollMessages();
    });

    canMessagesBeUpdatedRef.current = false;
    await sendMessage(chatId, textareaValue);
    canMessagesBeUpdatedRef.current = true;
  };

  const updateMessages = async () => {
    if (!chatId) return;

    const messages = await getMessages(chatId);

    if (canMessagesBeUpdatedRef.current) {
      setMessages(messages);
      setIsLoading(false);
    }
  };

  const scrollMessages = () => {
    window.scrollTo(0, document.body.scrollHeight);
  };

  if (!chatId || !userId || !photoId) return null;

  return (
    <div ref={chatRef} className="chat">
      <div className="top">
        <Link to="/chats">
          <span
            onClick={() => {
              setIsLoading(true);
            }}
            className="material-symbols-outlined icon back"
          >
            arrow_back
          </span>
        </Link>
        <Link
          to={`/chats/profile/${userId}`}
          className="avatar"
          style={{ backgroundImage: `url(${getPhotoURL(userId, photoId)})` }}
        />
        <Link to={`/chats/profile/${userId}`} className="name">
          {name}
        </Link>
      </div>

      {!isLoading && !messages.length && (
        <div className="no-messages">
          <p>Є взаємна симпатія, починай спілкуватись</p>
          <span className="material-symbols-rounded">mood</span>
        </div>
      )}

      <div className="messages">
        {messages.map((message, i) => (
          <div
            className="message"
            style={{
              alignSelf: message.isByMe ? "flex-end" : "flex-start",
            }}
            key={i}
          >
            {message.payload
              .trim()
              .split("\n")
              .map((payload, index) => (
                <div key={index}>
                  <span>{payload}</span>
                  <br />
                </div>
              ))}
          </div>
        ))}
      </div>

      <div className="send-container">
        <textarea
          value={textareaValue}
          onChange={(e) => setTextareaValue(e.target.value)}
          placeholder="Повідомлення"
        />
        <button
          onClick={onSend}
          className="material-symbols-outlined icon send-button"
        >
          send
        </button>
      </div>
    </div>
  );
};

export default Chat;
